:root {
  --text-primary: #fff;
  --bg-secondary: #111;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
  scrollbar-width: thin;
}

*::selection {
  background-color: var(--bg-primary);
  color: var(--text-primary);
}

section {
  padding: 5em;
  background-color: #000;
  color: var(--text-primary);
}

section h2 {
  position: relative;
  font-size: 2.5em;
  margin-bottom: 1em;
}

section h2::after {
  content: "";
  position: absolute;
  bottom: -0.2em;
  left: 0;
  height: 0.1em;
  width: 2em;
  background-color: var(--bg-primary);
}

.intro {
  width: 100%;
  height: 100vh;
  cursor: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(to right, #0007, transparent);
}

.intro canvas {
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.intro::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3em;
  background: linear-gradient(to top, #000, transparent);
  z-index: 2;
}

.intro h1 {
  font-size: 3em;
  font-weight: lighter;
  user-select: none;
}

.intro h1 > span {
  display: block;
}

.intro h1 > span:last-child {
  font-size: 0.7em;
}

.intro a,
form button {
  display: inline-block;
  position: relative;
  margin-top: 1em;
  outline: none;
  border: 1px solid var(--bg-primary);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  font-size: 1.2em;
  padding: 0.5em 1.5em;
  border-radius: 0.1em;
  cursor: pointer;
  text-decoration: none;
  z-index: 2;
}

.skills__list,
.projects__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.skills__list div h3 {
  font-size: 1.5em;
  margin-bottom: 0.5em;
}

.skills__list div ul {
  list-style: lower-greek;
  margin-bottom: 2em;
}

.skills__list div ul li {
  margin-left: 1.5em;
  font-size: 1.2em;
  margin-bottom: 0.7em;
}

.experience__list {
  display: flex;
  gap: 2em;
  flex-wrap: wrap;
}

.experience__list div {
  flex: 1;
}

.experience__list div h3 {
  font-size: 1.5em;
  margin-bottom: 0.3em;
}

.experience__list div h4 {
  font-size: 1.2em;
  margin-bottom: 1em;
}

.experience__list div p {
  color: #777;
}

.projects__list > div {
  width: 15em;
  margin-bottom: 2em;
}

.projects__list > div > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 1.2em;
  font-size: 1.5em;
}

.projects__list > div > div > a {
  color: var(--text-primary);
}

.projects__list div img {
  width: 100%;
  height: 15em;
  box-shadow: 0 0 1em 0.2em #fff1;
  border-radius: 0.1em;
  transition: transform 500ms;
}

.projects__list div img:hover {
  transform: scale(1.1);
}

.projects__list div p {
  margin-top: 0.7em;
  font-weight: normal;
  color: #777;
}

.about__contact {
  display: flex;
  flex-wrap: wrap;
}

.about__contact > div {
  width: 50%;
}

.about__contact > div > p {
  padding-right: 2em;
  padding-bottom: 2em;
  font-size: 1.25em;
}

form {
  display: flex;
  flex-direction: column;
}

form div {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
}

form label {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
  font-size: 1.1em;
}

form label input,
form label textarea {
  margin-top: 0.6em;
  margin-right: 2em;
  padding: 0.4em 0.5em;
  outline: none;
  border: none;
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  font-size: 1em;
  resize: none;
}

form label input:focus,
form label textarea:focus {
  border: none;
  outline: none;
}

form button {
  /* added styles same as intro button see up */
  width: max-content;
  padding: 0.3em 1.5em;
  margin-top: 0.5em;
  margin-right: 1em;
}

form button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

form div .alert {
  padding: 0.5em 1.5em;
  margin-top: 0.5em;
  background-color: #0f07;
  border-radius: 0.5em;
  display: none;
}

form div .alert.visible {
  display: unset;
}

footer {
  background-color: var(--bg-secondary);
  padding: 2em;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  justify-content: center;
}

footer div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.social__links {
  display: flex;
  list-style-type: none;
  margin-bottom: 0.5em;
}

.social__links li {
  margin-left: 0.8em;
  font-size: 1.5em;
}

.social__links li a {
  color: var(--text-primary);
}

footer p span {
  color: var(--bg-primary);
}

@media (max-width: 1000px) {
  .intro {
    background: #0005;
  }

  section {
    font-size: 15px;
    padding: 2.5em;
  }

  .intro {
    font-size: 12px;
  }

  .about__contact {
    flex-wrap: wrap;
  }

  .about__contact > div,
  .about__contact > div > form > div > label {
    width: 100%;
  }
}
